.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* justify-content: center; */
	/* font-size: calc(10px + 2vmin); */
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

th {
	width: 50px;
	border: 1px #333 solid; /* 枠線をつける */
	background-color: #727272;
	padding: 5px;
}
td {
	width: 50px;
	border: 1px #333 solid; /* 枠線をつける */
	text-align: left;
	padding: 5px;
}
table {
	border-collapse: collapse;
	width: 96%;
}
.category {
	text-align: center;
	width: 15%;
}
.link_title {
	width: 25%;
}
.description {
	width: 30%;
}
.link {
	width: 30%;
}
a:visited {
	color: darkgrey;
}
